import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { AllServicesService } from 'src/app/all-services.service';
import { Category } from 'src/app/model/category.model';
import { Inventory } from 'src/app/model/inventory.model';
import { Room } from 'src/app/model/room.model';

@Component({
  selector: 'app-inventory-edit',
  templateUrl: './inventory-edit.component.html',
  styleUrls: ['./inventory-edit.component.css']
})
export class InventoryEditComponent implements OnInit {

  constructor(private allServices: AllServicesService,
    private router: Router) {
  }

  ngOnInit(): void {
    this.selectedInventory = this.allServices.getInvetory();
    this.roomNames = this.allServices.roomNames;
    this.categories = this.allServices.categories;
    this.editIndex = this.allServices.editInventoryIndex;
  }

  selectedInventory: Inventory;
  roomNames: Room[];
  categories: Category[];
  widthCondition: boolean = false;
  editIndex: number;

  closePopup() {
    this.widthCondition = true;
    this.allServices.enableTextbox();
    this.router.navigate(['/']);
  }

  onEditInventory(form: NgForm) {
    const updateInventory = new Inventory(form.value.itemName,new Category(form.value.category),new Room(form.value.room),
      form.value.location,form.value.addInfo,true);
      this.allServices.updateInventory(updateInventory);
  }
}
