import { Component, ElementRef, EventEmitter, OnInit, Output, ViewChild } from '@angular/core';
import { AllServicesService } from '../all-services.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  constructor(private allServices: AllServicesService,
    private router: Router) { }

  ngOnInit(): void {
    this.allServices.resetTextbox.subscribe(
      (value) => {
        this.searchBox.nativeElement.value = value;
      }
    );
  }

  isTextboxDisabled$ = this.allServices.isTextboxDisabled$;
  @Output() searchTextChanged = new EventEmitter<string>();
  searchText: string = '';
  @ViewChild('searchBox') searchBox: ElementRef;
  disableTextboxOnCreateInventory() {
    this.allServices.disableTextbox();
    if (!this.allServices.roomNames) {
      alert('Set Rooms first and comback !');
      this.router.navigate(['/']);
    }
  }

  disableTextboxOnCreateRoom() {
    this.allServices.disableTextbox();
  }

  disableTextboxOnAddCategory() {
    this.allServices.disableTextbox();
  }

  onSearch() {
    const originalInventories = this.allServices.inventories.slice();
    const filteredInventories = originalInventories.filter(
      (inventory) => {
        return inventory.itemName.toLowerCase().includes(this.searchText.toLowerCase())
      }
    )
    this.allServices.setFilteredInventory(filteredInventories);
  }

}
