import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PopoverFormsComponent } from './popover-forms/popover-forms.component';
import { CreateNewComponent } from './inventory/create-new/create-new.component';
import { InventoryEditComponent } from './inventory/inventory-edit/inventory-edit.component';
import { NotFoundComponent } from './inventory/not-found/not-found.component';
import { LayoutComponent } from './layout/layout.component';


const routes: Routes = [
  {path: '', component: LayoutComponent, pathMatch: 'full'},
  {path: 'add-room', component: PopoverFormsComponent},
  {path: 'inventory', component: LayoutComponent},
  {path: 'add-new-inventory', component: CreateNewComponent},
  {path: 'edit-inventory', component: InventoryEditComponent},


  {path: '**', component: NotFoundComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
