import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'displayEllipsis'
})
export class DisplayEllipsisPipe implements PipeTransform {

  transform(value: string): string {
    if(value.length <= 8) {
      return value;
    }
    else {
      return value.substring(0, 8) + '...';
    }
  }

}
