import { AfterContentChecked, AfterContentInit, AfterViewChecked, AfterViewInit, Component, DoCheck, OnChanges, OnInit } from '@angular/core';
import { AllServicesService } from '../all-services.service';
import { Inventory } from '../model/inventory.model';
import { Room } from '../model/room.model';

@Component({
  selector: 'app-inventory-item-location',
  templateUrl: './inventory-item-location.component.html',
  styleUrls: ['./inventory-item-location.component.css']
})
export class InventoryItemLocationComponent implements OnInit {

  constructor(private allServices: AllServicesService) { }

  ngOnInit(): void {
this.allServices.selectedInventory$.subscribe(
  (details) => {
this.itemName = details.itemName;
this.roomName = details.roomNames;
this.location = details.location;
this.addInfo = details.addInfo;
  }
)
  }

  selectedInventory: Inventory;
  itemName: string;
  roomName: Room;
  location: string;
  addInfo: string;
}
