import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { AllServicesService } from '../all-services.service';
import { Inventory } from '../model/inventory.model';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-inventory',
  templateUrl: './inventory.component.html',
  styleUrls: ['./inventory.component.css']
})
export class InventoryComponent implements OnInit, OnDestroy {

  constructor(private allServices: AllServicesService) { }

  ngOnInit(): void {
   this.subscription = this.allServices.inventoriesChanged.subscribe(
      (data: Inventory[]) => {
        this.inventories = data;
      }
    )

    if(this.allServices.filterInventory$) {
      this.allServices.filterInventory$.subscribe(
        (inventory) => {
          this.inventories = inventory;
        }
      )
    }
    else {
      this.inventories =this.allServices.getInventories();
    }
    this.inventories =this.allServices.getInventories();
    
  }

  inventories: Inventory[];
  subscription: Subscription;
  @Input() items: string[] = [];
  filteredItems: string[] = [];



  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  onSearchTextChanged(searchText: string) {
    this.filteredItems = this.items.filter(
      item => item.toLowerCase().includes(searchText.toLowerCase())
    );
  }

}
