import { Injectable } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { Room } from './model/room.model';
import { Category } from './model/category.model';
import { Inventory } from './model/inventory.model';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class AllServicesService {

  constructor(private router: Router) { }

  private isTextboxDisabled = new BehaviorSubject<boolean>(false);
  isTextboxDisabled$ = this.isTextboxDisabled.asObservable();
  roomNames: Room[] = [
    new Room('Living Room'),
    new Room('Bed Room'),
    new Room('Study Room'),
    new Room('Utility Room')
  ];
  categories: Category[] = [
    new Category('Electronics'),
    new Category('Stationery'),
    new Category('Documents'),
    new Category('Books'),
    new Category('Others')
  ];
  inventories: Inventory[] = [
    new Inventory('Sony TV', new Category('Electronics'), new Room('Living Room'), 'Attached to South facing Wall', 'TV is 10 year old', false),
    new Inventory('Pendrive', new Category('Electronics'), new Room('Bed Room'), 'Entrance Cupboard', 'Storage Box', true),
    new Inventory('Canon Printer', new Category('Electronics'), new Room('Bed Room'), 'Computer Desk', 'Catridge needs filling', true),
    new Inventory('Certificates', new Category('Documents'), new Room('Study Room'), 'Books Cupboard', 'Certificates file', true),
    new Inventory('Engineering Books', new Category('Books'), new Room('Study Room'), 'Books Cupboard', 'Top Shelf', true),
    new Inventory('Laundry Detergent', new Category('Others'), new Room('Utility Room'), 'Cupboard', 'Bottom Shelf', true),
    new Inventory('Marker', new Category('Stationery'), new Room('Study Room'), 'Books Cupboard', 'Stationery Box', false)
  ];
  inventoriesChanged = new Subject<Inventory[]>();
  selectedInventory = new Subject<Inventory>();
  selectedInventory$ = this.selectedInventory.asObservable();
  electronicsFilteredArray: Inventory[];
  electronicsFilteredArray$ = new Subject<Inventory[]>();
  stationeryFilteredArray: Inventory[];
  stationeryFilteredArray$ = new Subject<Inventory[]>();
  documentsFilteredArray: Inventory[];
  documentsFilteredArray$ = new Subject<Inventory[]>();
  booksFilteredArray: Inventory[];
  booksFilteredArray$ = new Subject<Inventory[]>();
  othersFilteredArray: Inventory[];
  othersFilteredArray$ = new Subject<Inventory[]>();
  editInventory: Inventory;
  editInventoryIndex: number;
  private filterInventorySubject = new Subject<Inventory[]>();
  filterInventory$ = this.filterInventorySubject.asObservable()
  unReturnedInventory = new Subject<number>();
  count: number = 0;
  filteredInventory: Inventory[];
  resetTextbox = new Subject<string>();

  setFilteredInventory(filterInventory: Inventory[]) {
    this.filteredInventory = filterInventory;
    this.filterInventorySubject.next(filterInventory)
  }

  calculateUnReturnedInventory() {
    this.count = 0;
    this.inventories.filter(
      (inventory) => {
        if (inventory.status === false) {
          this.count++

        }
      }
    )
    this.unReturnedInventory.next(this.count);
    return this.count
  }

  disableTextbox() {
    this.isTextboxDisabled.next(true);
  }

  enableTextbox() {
    this.isTextboxDisabled.next(false);
  }

  setRoomName(roomNames: Room) {
    this.roomNames.push(roomNames);
  }

  addCategory(categoryName: Category) {

    this.categories.push(categoryName);
  }

  addInventory(newInventory: Inventory) {
    this.inventories.push(newInventory);
    this.categoryCount();
    this.inventoriesChanged.next(this.inventories.slice());
  }

  onSelectedInventory(index: number) {
    if (this.filteredInventory) {
      this.selectedInventory.next(this.filteredInventory[index]);
    } else {
      this.selectedInventory.next(this.inventories[index]);
    }
  }

  getInventories() {
    return this.inventories.slice();
  }

  setInventory(id: number) {
    if(this.filteredInventory) {
      this.editInventory = this.filteredInventory[id];
    } else {
      this.editInventory = this.inventories[id];
    }
    
    this.editInventoryIndex = id;
    // this.router.navigate(['/EditInventory']);
  }

  getInvetory() {
    return this.editInventory;
  }

  updateInventory(updateInventory: Inventory) {
    this.inventories[this.editInventoryIndex].itemName = updateInventory.itemName;
    this.inventories[this.editInventoryIndex].categories.names = updateInventory.categories.names;
    this.inventories[this.editInventoryIndex].roomNames.names = updateInventory.roomNames.names;
    this.inventories[this.editInventoryIndex].location = updateInventory.location;
    this.inventories[this.editInventoryIndex].addInfo = updateInventory.addInfo;
    this.inventoriesChanged.next(this.inventories.slice());
  }

  deleteInventory(id: number) {
    if (this.filteredInventory) {
      const itemName = this.filteredInventory[id].itemName;
      this.filteredInventory.splice(id, 1);
      this.inventories = this.inventories.filter(
        (inventory) => inventory.itemName !== itemName
      );

      this.inventoriesChanged.next(this.inventories.slice());
      this.categoryCount();
    } else {
      this.inventories.splice(id, 1);
      this.inventoriesChanged.next(this.inventories.slice());
      this.categoryCount();
    }
this.resetTextbox.next('');
  }

  statusChange(id: number) {
    if(this.filteredInventory) {
      this.filteredInventory[id].status = !this.filteredInventory[id].status;
      this.calculateUnReturnedInventory();
      this.inventoriesChanged.next((this.inventories.slice()));
    } else {
      this.inventories[id].status = !this.inventories[id].status;
      this.calculateUnReturnedInventory();
      this.inventoriesChanged.next((this.inventories.slice()));
    }
    
  }

  categoryCount() {
    this.electronicsFilteredArray = this.inventories.filter(
      (array) => {
        if (array.categories.names === 'Electronics') {
          return array;
        }
      }
    );
    this.electronicsFilteredArray$.next(this.inventories.filter(
      (array) => {
        if (array.categories.names === 'Electronics') {
          return array;
        }
      }
    ));
    this.stationeryFilteredArray = this.inventories.filter(
      (array) => {
        if (array.categories.names === 'Stationery') {
          return array;
        }
      }
    );
    this.stationeryFilteredArray$.next(this.inventories.filter(
      (array) => {
        if (array.categories.names === 'Stationery') {
          return array;
        }
      }
    ));
    this.documentsFilteredArray = this.inventories.filter(
      (array) => {
        if (array.categories.names === 'Documents') {
          return array;
        }
      }
    );
    this.documentsFilteredArray$.next(this.inventories.filter(
      (array) => {
        if (array.categories.names === 'Documents') {
          return array;
        }
      }
    ));
    this.booksFilteredArray = this.inventories.filter(
      (array) => {
        if (array.categories.names === 'Books') {
          return array;
        }
      }
    );
    this.booksFilteredArray$.next(this.inventories.filter(
      (array) => {
        if (array.categories.names === 'Books') {
          return array;
        }
      }
    ));
    this.othersFilteredArray = this.inventories.filter(
      (array) => {
        if (array.categories.names === 'Others') {
          return array;
        }
      }
    );
    this.othersFilteredArray$.next(this.inventories.filter(
      (array) => {
        if (array.categories.names === 'Others') {
          return array;
        }
      }
    ));
  }

}
