import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { AllServicesService } from 'src/app/all-services.service';
import { Inventory } from 'src/app/model/inventory.model';

@Component({
  selector: 'app-inventory-item',
  templateUrl: './inventory-item.component.html',
  styleUrls: ['./inventory-item.component.css']
})
export class InventoryItemComponent implements OnInit {

  constructor(private allServices: AllServicesService,
    private router: Router
    ) { }

  ngOnInit(): void {
  }

  @Input() currentInventory: Inventory;
  @Input() id: number;

  onClickInventory() {
    this.allServices.onSelectedInventory(this.id);
      }

      onEdit() {
        this.allServices.setInventory(this.id);
this.router.navigate(['/edit-inventory']);
      }

      onDelete() {
        this.allServices.deleteInventory(this.id);
        this.allServices.calculateUnReturnedInventory();
      }

      statusChange() {
        this.allServices.statusChange(this.id);
        
      }
}
