import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { AllServicesService } from 'src/app/all-services.service';
import { Category } from 'src/app/model/category.model';
import { Inventory } from 'src/app/model/inventory.model';
import { Room } from 'src/app/model/room.model';

@Component({
  selector: 'app-create-new',
  templateUrl: './create-new.component.html',
  styleUrls: ['./create-new.component.css']
})
export class CreateNewComponent implements OnInit {

  constructor(
    private router: Router,
    private allServices: AllServicesService) { }

  ngOnInit(): void {
    this.roomNames = this.allServices.roomNames;
    this.categories = this.allServices.categories;
  }

  widthCondition: boolean;
  roomNames: Room[];
  categories: Category[];
  editMode: boolean = false;
  showMessage: boolean = false;

  closePopup() {
    this.widthCondition = true;
    this.allServices.enableTextbox();
    this.router.navigate(['/']);
  }

  onCreateInventory(form: NgForm) {
    const newInventory = new Inventory(form.value.itemName,new Category(form.value.category),new Room(form.value.room),
      form.value.location,form.value.addInfo,true);
      this.allServices.addInventory(newInventory);
      this.showMessage = true;
    setTimeout(() => {
      this.showMessage = false
    }, 2000);
  }
}
