import { Component, ElementRef, HostListener, OnInit, Renderer2, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { AllServicesService } from '../all-services.service';
import { NgForm } from '@angular/forms';
import { Room } from '../model/room.model';

@Component({
  selector: 'app-popover-forms',
  templateUrl: './popover-forms.component.html',
  styleUrls: ['./popover-forms.component.css']
})
export class PopoverFormsComponent implements OnInit {

  constructor(private router: Router,
    private allService: AllServicesService) { }

  ngOnInit(): void {
    if (this.router.url === '/CreateRoom') {
      this.allService.disableTextbox();
    }
  }

  widthCondition: boolean;
  @ViewChild('form') createRoomForm: NgForm;
  showMessage: boolean = false

  closePopup() {
    this.widthCondition = true;
    this.allService.enableTextbox();
    this.router.navigate(['/']);
  }

  onCreateRooms(form: NgForm) {
    const array = form.value.enterRooms.split(",");
    for(let i=0; i<array.length;i++) {
      const newRomm = new Room(array[i]);
      this.allService.setRoomName(newRomm);
    }
    this.showMessage = true;
    setTimeout(() => {
      this.showMessage = false
    }, 2000);
  }


}
