import { Component, OnInit, ViewChild } from '@angular/core';
import { AllServicesService } from '../all-services.service';
import { BaseChartDirective } from 'ng2-charts';

@Component({
  selector: 'app-chart-representation',
  templateUrl: './chart-representation.component.html',
  styleUrls: ['./chart-representation.component.css']
})
export class ChartRepresentationComponent implements OnInit {

  constructor(private allServices: AllServicesService) { }

  ngOnInit(): void {
    this.allServices.categoryCount();
    

    this.allServices.electronicsFilteredArray$.subscribe(
      (inventory) => {
        this.barChartData[0].data[0] = inventory.length;
        this.updateChart();
        this.calculateTotalInventory();
      }
    )
    this.allServices.stationeryFilteredArray$.subscribe(
      (inventory) => {
        this.barChartData[0].data[1] = inventory.length;
        this.updateChart();
        this.calculateTotalInventory();
      }
    )
    this.allServices.documentsFilteredArray$.subscribe(
      (inventory) => {
        this.barChartData[0].data[2] = inventory.length;
        this.updateChart();
        this.calculateTotalInventory();
      }
    )
    this.allServices.booksFilteredArray$.subscribe(
      (inventory) => {
        this.barChartData[0].data[3] = inventory.length;
        this.updateChart();
        this.calculateTotalInventory();
      }
    )
    this.allServices.othersFilteredArray$.subscribe(
      (inventory) => {
        this.barChartData[0].data[4] = inventory.length;
        this.updateChart();
        this.calculateTotalInventory();
      }
    )
    this.barChartData[0].data[0] = [this.allServices.electronicsFilteredArray ? this.allServices.electronicsFilteredArray.length : 0];
    this.barChartData[0].data[1] = [this.allServices.stationeryFilteredArray ? this.allServices.stationeryFilteredArray.length : 0];
    this.barChartData[0].data[2] = [this.allServices.documentsFilteredArray ? this.allServices.documentsFilteredArray.length : 0];
    this.barChartData[0].data[3] = [this.allServices.booksFilteredArray ? this.allServices.booksFilteredArray.length : 0];
    this.barChartData[0].data[4] = [this.allServices.othersFilteredArray ? this.allServices.othersFilteredArray.length : 0];
    if (this.chart) {
      this.chart.update();
    }
    this.unRetunedInventories = this.allServices.calculateUnReturnedInventory();
     this.allServices.unReturnedInventory.subscribe(
      (count) => {
        this.unRetunedInventories = count;
      }
     );
     this.calculateTotalInventory();
  }

  unRetunedInventories: number;
  totalInventories: number = 0;
  WidthPercentage: number = 80;
  HeightPercentage: number = 80;

  @ViewChild(BaseChartDirective) chart: BaseChartDirective;

  barChartOptions: any = {
    scaleShowVerticalLines: false,
    responsive: true,
    title: {
      text: 'Category Chart',
      display: true
    },
    legend: {
      labels: {
        fontSize: 12
      }
    },
    animation: {
      easing: 'easeOutBounce'
    },

  };

  barChartLabels: string[] = ['Electronics', 'Stationery', 'Documents', 'Books', 'Others'];

  barChartLegend = true;

  barChartData: any[] = [
    {
      data: [],
      label: 'Sample Data',
      backgroundColor: [
        'SlateBlue', 'DodgerBlue', 'Teal', 'MediumSeaGreen', 'LightSkyBlue'
      ],
      hoverBackgroundColor: [
        'SlateBlue', 'DodgerBlue', 'Teal', 'MediumSeaGreen', 'LightSkyBlue'
      ]
    }
  ];

  updateChart() {
    // this.barChartData[0].data[1] = length;
    if (this.chart) {
      this.chart.update();
    }
  }

  calculateTotalInventory() {
    this.totalInventories = 0;
    for(let i=0; i<this.barChartData[0].data.length; i++) {
      this.totalInventories += parseInt(this.barChartData[0].data[i]);
     }
  }
}
