import { Component, OnInit, ViewChild } from '@angular/core';
import { AllServicesService } from '../all-services.service';
import { Router } from '@angular/router';
import { NgForm } from '@angular/forms';
import { Category } from '../model/category.model';

@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.css']
})
export class CategoryComponent implements OnInit {

  constructor(private router: Router,
    private allService: AllServicesService) { }

  ngOnInit(): void {
    if (this.router.url === '/AddCategory') {
      this.allService.disableTextbox();
    }
  }

  widthCondition: boolean;
  @ViewChild('form') createRoomForm: NgForm;

  closePopup() {
    this.widthCondition = true;
    this.allService.enableTextbox();
    this.router.navigate(['/']);
  }

  onAddCategory(form: NgForm) {
    let categoryArr = form.value.enterCategory.split(",");
    categoryArr.forEach(arrayElement => {
      const newCategory = new Category(arrayElement);
    this.allService.addCategory(newCategory);
    });
    
  }


}
